export const sendMessage = async (chatId, message, markup = {}) => {
    await fetch(`https://api.telegram.org/bot${process.env.REACT_APP_TOKEN}/sendMessage`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            chat_id: chatId,
            text: message,
            parse_mode: 'HTML',
            reply_markup: markup
        })
    });
}