import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import useTelegram from '../../hooks/useTelegram';
import { privateChanneButtons } from '../../utils/const';
import './PrivateChannel.css';


const PrivateChannel = ({ setPaymentData }) => {
    const navigate = useNavigate();
    const { tg } = useTelegram();

    useEffect(() => {
        tg.BackButton.hide();
        tg.MainButton.hide();
    }, [tg]);
    
    const payment = (e) => {
        setPaymentData(e.target.textContent);
        localStorage.setItem('path', '/private-channel')
        navigate('/payment');
    }

    return (
        <div className='private-channe'>
            <p>Приватний канал <b>ULTRA PROFIT TRADE</b> об'єднує людей, які заробляють на трейдингу, обмінюються своїми думками, досвідом та баченням на ринок. Кожного дня публікується аналітика, яку робить наш ментор, по <b>BTC</b>, <b>ETH</b>, <b>DXY</b> та по <b>ALTCOINS</b>. Попри це - учасники приватного каналу мають можливість попросити аналіз по чомусь конкретному і їм ніхто ще не відмовляв.</p>
            <p>Місце, де ви зможете знайти однодумців та наставників!</p>
            <p>Наш приватний канал поділений на багато гілок для зручності:</p>
            <ul>
                <li><b>BTC</b> - гілка, де ми кожного дня публікуємо аналітику по BTC</li>
                <li><b>ETH</b> - гілка, де ми кожного дня публікуємо аналітику по ETH</li>
                <li><b>ALTCOINS</b> - гілка, де ми кожного дня публікуємо аналітику по ALTCOINS</li>
                <li><b>INDEX</b> - гілка, де ми кожного дня публікуємо аналітику по DXY, SP500</li>
                <li><b>FOREX</b> - гілка, де ми кожного дня публікуємо аналітику по FOREX</li>
                <li><b>SPOT</b> - гілка, де ми ділимось думками на рахунок активів на спот</li>
                <li><b>FAQ</b> - гілка, де описані правила нашого каналу</li>
                <li><b>Обговорення чартів</b> - гілка, де ми і учасники нашої групи обмінюються думками по ринку</li>
                <li><b>FLOOD</b> - гілка для спілкування на різні теми</li>
                <li><b>Плюсові угоди</b> - гілка, де показані плюсові угоди учасників, і наші</li>
                <li><b>Мінусові угоди</b> - гілка, де показані збиткові угоди учасників, і наші</li>
                <li><b>Питання</b> - гілка, де учасники можуть задати запитання, щоб вони не загубилися</li>
                <li><b>Психологія</b> - гілка, де ми публікуємо корисний матеріал по психології</li>
                <li><b>Announcements</b> - гілка, де ми оголошуємо новини нашого ком'юніті</li>
            </ul>
            <h3>Ціна</h3>
            {privateChanneButtons.map((button, index) => {
                return(
                    <button key={index} onClick={payment}>{button}</button>
                )
            })}
        </div>
    )
}

export default PrivateChannel;