import React, { useEffect, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import Header from './components/Header/Header';
import Form from './components/Form/Form';
import ModalWindow from './components/ModalWindow/ModalWindow';
import useTelegram from './hooks/useTelegram';
import './App.css';
import Study from './components/Study/Study';
import PrivateChannel from './components/PrivateChannel/PrivateChannel';
import Payment from './components/Payment/Payment';
import Profile from './components/Profile/Profile';


function App() {
    const [modalIsOpen, setIsOpen] = useState(false);
    const [isCloseApp, setIsCloseApp] = useState(false);
    const [imgHeight, setImgHeight] = useState(0);
    const [imgStatus, setImgStatus] = useState(false);
    const [modalMessage, setModalMessage] = useState('');
    const [paymentData, setPaymentData] = useState('');

    const { tg } = useTelegram();

    useEffect(() => {
        tg.expand();
        tg.ready();
    }, [tg])

    const openModal = (msg) => {
        setModalMessage(msg);
        setIsOpen(true);
    }
    const closeModal = () => {
        setIsOpen(false);
        if(isCloseApp) tg.close();
        
    }


    return (
        <div className="app">
            <ModalWindow 
                modalIsOpen = {modalIsOpen} 
                modalMessage = {modalMessage} 
                closeModal = {closeModal}
            />
            <Header 
                setImgHeight = {setImgHeight}
                setImgStatus = {setImgStatus}
            />
            {!imgStatus ? <div></div>:
            <Routes>
                <Route 
                    path={'form'} 
                    element={
                        <Form 
                            openModal = {openModal} 
                            setIsCloseApp = {setIsCloseApp}
                        />
                    }
                />
                <Route 
                    path={'study'} 
                    element={
                        <Study
                            setPaymentData = {setPaymentData}
                        />
                    }
                />
                <Route 
                    path={'private-channel'} 
                    element={
                        <PrivateChannel
                            setPaymentData = {setPaymentData}
                        />
                    }
                />
                <Route
                    path={'payment'}
                    element={
                        <Payment
                            paymentData = {paymentData}
                        />
                    }
                />
                <Route
                    path={'profile'}
                    element={
                        <Profile
                            imgHeight = {imgHeight}
                        />
                    }
                />
            </Routes>}         
        </div>
    );
}

export default App;
