import React, { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import useTelegram from '../../hooks/useTelegram';
import study from '../../img/study.png';
import {studyButtons} from '../../utils/const';
import './Study.css';


const Study = ({ setPaymentData }) => {
    const navigate = useNavigate();
    const { tg } = useTelegram();

    useEffect(() => {
        tg.BackButton.hide();
        tg.MainButton.hide();
    }, [tg]);
    
    const payment = (e) => {
        setPaymentData(e.target.textContent);
        localStorage.setItem('path', '/study');
        navigate('/payment');
    }

    return (
        <div className='study'>
            <p>Основа навчання - концепція <b>Smart Money</b> i <b>Price Acrion</b>.</p>
            <p>Синхрон цих двох складових дасть вам можливість торгувати значно якісніше та безпечніше. Трейдинг це поєднання інструментів, математики та психології, які в результаті роботи ведуть до дистанційного заробітку.</p>
            <p>Це навчання - протилежне до того, що є на просторах інтернету. Подібних курсів майже немає в Україні та СНГ.</p>
            <h3>Подібне навчання повністю змінить ваше уявлення про рух ціни та її поведінку.</h3>
            <p>Ми маємо досвід з усіма популярними курсами, де просто дається невеличкий розбір і даються методички після заняття, що погіршує сприйняття нової інформації.
            В нашому ж випадку ви маєте можливість постійно контактувати з вашим ментором, учнями. Будь-які питання або ситуації розбираються до повного розуміння.</p>
            <p>Навчання складається з 4 блоків</p>
            <ul>
                <li>
                    <b>Блок 1. ВСТУП.</b>
                    <p>Знайомство з концепцією та платформами. Словник трейдера.</p>
                </li>
                <li>
                    <b>Блок 2. ПОЧАТОК РОБОТИ. </b>
                    <p>Робота з графіками. Рух інституційних грошей. Структура та основи руху ціни.</p>
                </li>
                <li>
                    <b>Блок 3. ОСНОВНА РОБОТА.</b>
                    <p>Основна робота з інструментами. Робота із зонами інтересу.</p>
                </li>
                <li>
                    <b>Блок 4. ЗАКІНЧЕННЯ. </b>
                    <p>Побудова торгової стратегії. Поєднання знань. Психологія.</p>
                </li>
            </ul>
            <h3>Блок 1</h3>
            <p>В цей блок входить один урок, де у вас є можливість познайомитися з ментором, обговорити план занять, розібратися з платформами та розібрати концепцію, за якою ви будете навчатися.</p>
            <div className="lesson">
                <b>Урок 1 - Робота з платформою Trading View</b>
            </div>
            <h3>Блок 2</h3>
            <p>Даний блок включай в себе 4 уроки по психології руху інституціональних грошей. Проводиться детальний розбір, чому щось росте, чому щось падає, які причини впливають на це.</p>
            <div className="lesson">
                <b>Урок 2 - Структура ринку</b>
            </div>
            <ul className='list'>
                <li>Структура ринку</li>
                <li>Типи структури</li>
                <li>Структурні елементи</li>
                <li>BOS / FAKE BOS</li>
                <li>Таймфрейми + мультиструктура</li>
            </ul>
            <div className="lesson">
                <b>Урок 3 - Фібоначі + Імбаланс</b>
            </div>
            <ul className='list'>
                <li>Зона ОТЕ</li>
                <li>Преміум / Дискаунт маркет</li>
                <li>Закон сили</li>
                <li>Імбаланс</li>
            </ul>
            <div className="lesson">
                <b>Урок 4 - Ліквідність</b>
            </div>
            <ul className='list'>
                <li>Що таке ліквідність</li>
                <li>Види ліквідності</li>
                <li>Маніпуляції з ліквідністю(stb/bts)</li>
            </ul>
            <h3>Блок 3</h3>
            <p>В цьому блоці ми будемо працювати з основними інструментами, моделями та індикаторами, які допоможуть вам прийняти ПРАВИЛЬНЕ торгове рішення. Найбільш інформативна частина курсу.</p>
            <div className="lesson">
                <b>Урок 5 - Психологічні зони</b>
            </div>
            <ul className='list'>
                <li>Order Block</li>
                <li>Mitigation Block</li>
                <li>Breaker Block</li>
                <li>Sponsored candle</li>
                <li>WICK</li>
            </ul>
            <div className="lesson">
                <b>Урок 6 - Моделі розвороту та RSI</b>
            </div>
            <ul className='list'>
                <li>Індикатор RSI</li>
                <li>Three Drives Pattern</li>
                <li>Three Tap Setup</li>
                <li>Swing Failure Pattern</li>
                <li>QA model</li>
            </ul>
            <div className="lesson">
                <b>Урок 7 - Торгові сесії</b>
            </div>
            <ul className='list'>
                <li>Opens</li>
                <li>Killzones</li>
                <li>Індикатори торгових сесії</li>
                <li>Power of Three (PO3)</li>
            </ul>
            <div className="lesson">
                <b>Урок 8 - Боковик і Вайкофф</b>
            </div>
            <ul className='list'>
                <li>Робота в Боковику</li>
                <li>Девіації</li>
                <li>Вайкофф</li>
                <li>Акумуляція</li>
                <li>Дистрибуція</li>
            </ul>
            <div className="lesson">
                <b>Урок 9 - Робота в POI</b>
            </div>
            <ul className='list'>
                <li>Робота з зонами інтересу</li>
                <li>Supply / Demand zones</li>
            </ul>
            <h3>Блок 4</h3>
            <p>Заключний блок курсу. Узагальнення інформації. Побудова вашої торгової стратегії, дотримуючись якої, ви зможете почати заробляти. Робота із дисципліною та психологією.</p>
            <div className="lesson">
                <b>Урок 10 - Final</b>
            </div>
            <ul className='list'>
                <li>Психологія</li>
                <li>Дисципліна</li>
                <li>Торгова стратегія </li>
                <li>Можливості</li>
            </ul>
            <h3>Як відбувається навчання</h3>
            <p>Навчання проходить в оффлайн та онлайн режимі.</p>
            <p>Присутні як групові так і індивідуальні форми.</p>
            <p>Перед уроком у вас буде методичка з якою ви повинні ознайомитися.</p>
            <p>1) Заняття починається з розбором помилок у домашньому завданні кожного учня. Так, ми працюємо з кожним індивідуально. Якщо це онлайн формат - у кожного є свій канал, де ведеться розмова і розбір помилок з ментором.<br/>2) Після цього ми переходимо до розбору теми, яку ви проходили вдома по методичках. Розбираються усі моменти і нюанси в роботі.<br/>3) Наступний крок це перехід до практичного застосування матеріалу та знань.<br/>Методички це лише 30% процесу. Основна та найважливіша інформація проводиться на уроці.</p>
            <p>У кожного учня буде окремий чат для домашніх завдань, де ви особисто будете опрацьовувати ваші питання з ментором.</p>
            <p>Приклад фото з онлайн навчання:</p>
            <img src={study} alt=""/>
            <h3>Ціна</h3>
            {studyButtons.map((button, index) => {
                return(
                    <button key={index} onClick={payment}>{button}</button>
                )
            })}
        </div>  
    )
}

export default Study;