import React from 'react';
import Modal from 'react-modal';
import './ModalWindow.css';


const customStyles = {
    content: {
        width: '60%',
        padding: '40px',
        top: '35%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        background: '#0c0c0c',
        border: 'none',
        borderRadius: '7px'
    }
};

Modal.setAppElement('#root');
Modal.defaultStyles.overlay.backgroundColor = 'rgba(255, 255, 255, 0.4)';

const ModalWindow = ({ modalIsOpen, modalMessage, closeModal }) => {
    return (
        <div>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
            >
                <p className='close' onClick={closeModal}>&#x2715;</p>
                <div className='message'>
                    <p>{modalMessage}</p>
                </div>
            </Modal>
        </div>
    )
}

export default ModalWindow;

