import React, { useEffect, useRef } from 'react';
import logo from '../../img/logo.png';
import './Header.css';


const Header = ({ setImgStatus, setImgHeight }) => {
    const img = useRef(null);

    useEffect(() => {
        // console.log(img.current.height);
        setImgHeight(img.current.height);
    });

    const onLoadCheck = () => setImgStatus(true);
 
    return (
        <div className='header'>
            <img 
                ref={img}
                src={logo} 
                onLoad={onLoadCheck} 
                alt=""
            />
        </div>
    )
}

export default Header;