import React, { useCallback, useEffect, useState } from 'react';
import useTelegram from '../../hooks/useTelegram';
import { phone as checkPhone } from 'phone';
import * as checkEmail from 'email-validator';
import { getData, setData } from '../../utils/database';
import { keyboardMenu } from '../../utils/const';
import './Form.css';
import { sendMessage } from '../../utils/telegram';


const Form = ({ openModal, setIsCloseApp }) => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');

    const { tg, id } = useTelegram();


    useEffect(() => {
        tg.onEvent('mainButtonClicked', validData);
        return () => {
            tg.offEvent('mainButtonClicked', validData);
        }
    });

    useEffect(() => {
        tg.MainButton.setParams({
            text: 'Підтвердити',
            text_color: '#0c0c0c',
            color: '#d5ad79'
        });
    });


    useEffect(() => {
        if(name && email && phone) {
            tg.MainButton.show();
        }
        else {
            tg.MainButton.hide();
        }
    }, [name, email, phone, tg]);

    useEffect(() => {
        getData(`Users/${id}`).then((data) => {
            if(data) {
                openModal('Ви вже заповнили анкету!');
                setIsCloseApp(true);
            }
        });
    }, [id, openModal, setIsCloseApp, tg]);

    const sendData = useCallback(async (phoneNumber) => {
        await sendMessage(id, 'Дякуємо! Анкету успішно заповнено.', keyboardMenu);
        let data = {};
        data[`Users/${id}`] = {
            name: name[0].toUpperCase() + name.slice(1).toLowerCase(),
            email,
            phone: phoneNumber,
        }
        await setData(data);
        tg.close();
    }, [id, tg, name, email])
        
    const validData = useCallback(() => {
        const isValidEmail = checkEmail.validate(email);
        if(!isValidEmail) return openModal('Некоректна електронна адреса!');
        const { isValid , phoneNumber } = checkPhone(phone, { country: 'UA' });
        if(!isValid) return openModal('Некоректний номер телефону!');
        return sendData(phoneNumber);
        
    }, [email, openModal, phone, sendData])

    const onChangeName = (e) => setName(e.target.value);
    const onChangeEmail = (e) => setEmail(e.target.value);
    const onChangePhone = (e) => setPhone(e.target.value);

    return (
        <div className='form'>
            <div className='name'>
                <input 
                    type="text" 
                    placeholder="Ім'я"
                    value={name}
                    onChange={onChangeName}
                />
            </div>
            <div className='email'>
                <input 
                    type="text" 
                    placeholder="Електронна адреса"
                    value={email}
                    onChange={onChangeEmail}
                />
            </div>
            <div className='phone'>
                <input 
                    type="text" 
                    placeholder="Номер телефону"
                    value={phone}
                    onChange={onChangePhone}
                />
            </div>
        </div>
    )
}

export default Form;