import React, { useEffect, useRef, useState } from 'react';
import useTelegram from '../../hooks/useTelegram';
import { getData } from '../../utils/database';
import './Profile.css';


const Profile = ({ imgHeight }) => {
    const [payments, setPayments] = useState([]);
    const { id } = useTelegram();

    const paymentsRef = useRef(null);

    useEffect(() => {
        if(payments.length) {
            paymentsRef.current.style.height = `calc(100vh - ${imgHeight + 40}px)`; 
        }
        else {
            paymentsRef.current.style.height = `calc(100vh - ${imgHeight + 100}px)`; 
        }

    },[imgHeight, payments]);

    useEffect(() => {
        getData(`Users/${id}/Payments`).then((data) => {
            if(data) {
                for(let id in data) {
                    const { date, tariff, sum, status } = data[id];
                    setPayments(prevState => {
                        const block = <div key={id} className='payment-block'>
                            <h4>Оплата №{id}</h4>
                            <p>Дата: {date}</p>
                            <p>Вибраний тариф: {tariff}</p>
                            <p>Сума до оплати: {sum}$</p>
                            <p>Статус: {status}</p>
                        </div>
                        return [...prevState, block]
                    });
                }
            }
        });
    }, [id]);

    return (
        <div className='profile'>
            <div className='payments' ref={paymentsRef}>
                {payments.length ? 
                    payments.reverse() 
                    :
                    <h3>Наразі немає жодної оплати</h3>
                }
            </div>
        </div>
    )
}

export default Profile;