import React, { useCallback, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import useTelegram from '../../hooks/useTelegram';
import wallet from '../../img/wallet.jpg';
import { getData, setData } from '../../utils/database';
import { formatDate } from '../../utils/helpers';
import { sendMessage } from '../../utils/telegram';
import './Payment.css';


const Payment = ({ paymentData }) => {
    const navigate = useNavigate();
    const { tg, id } = useTelegram(); 

    const [tariff, setTariff] = useState(''); 
    const [sum, setSum] = useState(0); 

    const turnBack = useCallback(() => {
        navigate(localStorage.getItem('path'));
    }, [navigate]); 

    useEffect(() => {
        tg.BackButton.show();
        tg.onEvent('backButtonClicked', turnBack);
        return () => {
            tg.offEvent('backButtonClicked', turnBack);
        }
    }, [tg, turnBack]);

    useEffect(() => {
        tg.MainButton.show();
        tg.MainButton.setParams({
            text: 'Підтвердити оплату',
            text_color: '#0c0c0c',
            color: '#d5ad79'
        });
    });

    useEffect(() => {
        tg.onEvent('mainButtonClicked', payment);
        return () => {
            tg.offEvent('mainButtonClicked', payment);
        }
    });

    useEffect(() => {
        window.scrollBy(0, -1000);
    });

    useEffect(() => {
        if(!paymentData) navigate(localStorage.getItem('path'));
    }, [paymentData, navigate]);

    useEffect(() => {
        const tmp = paymentData.split(' - ');
        if(paymentData.includes('заняття')) {
            setTariff(tmp[0]);
            setSum(parseInt(tmp[1]));
        }
        else {
            setTariff(`Підписка на приватний канал(${tmp[0]})`);
            setSum(parseInt(tmp[1]));
        }
    }, [paymentData, tariff, sum]);

    const payment = useCallback(async () => {
        const date = new Date();
        const paymentId = date.getTime();
        const data = {};
        data[`Users/${id}/Payments/${paymentId}`] = {
            date: formatDate(date),
            tariff,
            sum,
            status: 'Очікує перевірки',
        }
        await setData(data);
        await sendMessage(id, `Дякуємо! Незабаром з вами зв'яжеться адміністратор для підтверження оплати.`);
        const markup = {
            inline_keyboard: [
                [{text: 'Підтвердити ✅', callback_data: JSON.stringify({btn: 'confirm', user: id, paymentId})}],
                [{text: 'Відхилити ❌', callback_data: JSON.stringify({btn: 'reject', user: id, paymentId})}]
            ]
        }
        const { name, email, phone } = await getData(`Users/${id}`);
        const message = `<b>Оплата №${paymentId}</b>\n\nІм'я: <i><a href="tg://user?id=${id}">${name}</a></i>\nЕлектронна адреса: <i>${email}</i>\nНомер телефону: <i>${phone}</i>\nВибраний тариф: <i>${tariff}</i>\nСума до оплати: <i>${sum}$</i>\nСтатус: <i>Очікує перевірки</i>`
        await sendMessage('-1001768477651', message, markup);
        tg.close();
    }, [id, sum, tariff, tg])

    return (
        <div className='payment'>
            <p><b>Вибраний тариф:</b> {tariff}</p>
            <p><b>Сума до оплати:</b> {sum}$</p>
            <p><b>Спосіб оплати:</b> USDT (TRC20)</p>
            <p>Для оплати переведіть вказану суму за реквізитами нижче:</p>
            <img src={wallet} alt="" />
            <div className="wallet">
                <p>TYfoNKscMhK4i6p4pzwMUskn2tp6uhH5yE</p>
            </div>
            <p><b>Зверніть увагу!</b> Відправте суму, яка вказана вище. У цю суму комісії не входять, вони стягуються окремо блокчейном. Якщо оплата пройшла успішно і з вашого рахунку списались кошти, обов'язково нажміть кнопку <i>«Підтвердити оплату»</i>, після цього з вами зв'яжеться адміністратор для підтвердження.</p>
        </div>
    )
}

export default Payment;